import React, { useEffect, useState } from "react";

import { Row, Col, Input, Card, Pagination, Form } from "antd";
import { Table, Space } from "antd";
import Swal from "sweetalert2";

import { Search } from "react-iconly";
import { connect } from "react-redux";

import MasterPageTitle from "../../../../layout/components/content/master-page-title";

import AddFormula from "./addFormula";

import {
  getFormulas,
  formulaEdit,
  formulaDelete,
  isModelVisible,
} from "../../../../store/Masters/Formulas/formulaSlice";

function Formula(props) {
  useEffect(() => {
    props.getFormulas({ page: 1, search: "", page_size: 10 });
  }, []);

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "5%",
      // sorter: (a, b) => a.index - b.index,
    },
    {
      title: "Code",
      dataIndex: "code",
      // onFilter: (value, record) => record.address.startsWith(value),
      filterSearch: true,
      width: "35%",
    },
    {
      title: "Formula Name",
      dataIndex: "name",

      filterMode: "tree",
      filterSearch: true,
      // onFilter: (value, record) => record.name.includes(value),
      width: "40%",
    },
    {
      title: "Formula",
      dataIndex: "formula",

      filterMode: "tree",
      filterSearch: true,
      // onFilter: (value, record) => record.name.includes(value),
      width: "40%",
    },
    {
      title: "Actions",
      key: "action",
      sorter: true,
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
          <i
            className="ri-edit-box-line hp-mr-2 "
            style={{ cursor: "pointer", color: "#fc6f03" }}
            onClick={() => {
              props.formulaEdit(row);
              props.isModelVisible(true);
            }}
          ></i>
          <i
            className="ri-delete-bin-2-line "
            style={{ cursor: "pointer", color: "#ff0000" }}
            onClick={() => deleteRecord(row)}
          ></i>
        </Space>
      ),
    },
  ];

  const data = props.formulaslist.map((row, key) => ({
    ...row,
    index: key + 1,
    formula: row.formula,
  }));

  function deleteRecord(record) {
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        props.formulaDelete(record.id);
      }
    });
  }

  // function onShowSizeChange(current, pageSize) {
  //   props.getCountrys({ page_size: pageSize, page: 1 });
  // }
  function onPageChange(page, pageSize) {
    props.getFormulas({ page_size: pageSize, page: page == 0 ? 1 : page });
  }

  function onSearch(val) {
    props.getFormulas({ page: 1, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Formula Master"
        addModelTile="Add Formula"
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={12}>
              <Input
                className="hp-mb-12"
                placeholder="Search"
                prefix={
                  <Search set="curved" className="remix-icon" size={16} />
                }
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getFormulas({ page: 1, search: "" });
                  } else {
                    onSearch(e.target.value);
                  }
                }}
              />
            </Col>

            <Col span={24}>
              <Table
                columns={columns}
                dataSource={data}
                scroll={{ x: 500 }}
                pagination={false}
                size="middle"
              />
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                // current={props.?.page}
                // pageSize={props.?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                // onShowSizeChange={onShowSizeChange}
                onChange={onPageChange}
                on
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddFormula />
    </Row>
  );
}

function mapStateToProps({ formula }) {
  return {
    formulaslist: formula.formulaslist,
    noofpages: formula.noofpages,
    listCount: formula.listCount,
  };
}

export default connect(mapStateToProps, {
  getFormulas,
  formulaEdit,
  formulaDelete,
  isModelVisible,
})(Formula);
