import { Fragment, useState, useEffect } from "react";

import { connect } from "react-redux";

import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Badge, Row, Col } from "antd";
import {
  formulaAdd,
  isModelVisible,
  InputChangeValue,
} from "../../../../store/Masters/Formulas/formulaSlice";

export const isObjEmpty = (obj) => Object.keys(obj).length === 0;

const AddFormula = (props) => {
  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  // Adds New Country
  const handleAdd = () => {
    const obj = {
      id: 0,
      code:props.selectedData.code,
      name: props.selectedData?.name,
      formula: props.selectedData?.formula

    };
    if (obj.name != null || obj.name == "") {
      props.formulaAdd(obj);
    }
  };
  // Updates Country in Store
  const handleUpdate = () => {
    const formulaToUpdate = {
      id: props.selectedData.id,
    //   code:props.selectedData.code,
      name: props.selectedData?.name,
      formula: props.selectedData?.formula
    };
    props.formulaAdd(formulaToUpdate);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    if (props.selectedData.id == null || props.selectedData.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              handleAdd();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              handleUpdate();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.selectedData.id == null || props.selectedData.id == 0
        ? "Add"
        : "Update"}{" "}
      Formula
    </h5>
  );

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={416}
    >
      <Form layout="vertical">
      <Form.Item label="Code :">
          <Input
            id="Code"
            name="code"
            style={{ width: "100%" }}
            placeholder="Code"
            value={props.selectedData?.code}
            onChange={ChangeHandeler}
            rules={[
              { required: true, message: "Please input your Code!" },
            ]}
          />
        </Form.Item>
        <Form.Item label="Formula Name :">
          <Input
            id="Formula"
            name="name"
            style={{ width: "100%" }}
            placeholder="Formula Name"
            value={props.selectedData?.name}
            onChange={ChangeHandeler}
            rules={[
              { required: true, message: "Please input your Formula Name!" },
            ]}
          />
        </Form.Item>

        <Form.Item label="Formula :">
          <Input
            id="Formula"
            name="formula"
            style={{ width: "100%" }}
            placeholder="Formula Name"
            value={props.selectedData?.formula}
            onChange={ChangeHandeler}
            rules={[
              { required: true, message: "Please input your Formula Name!" },
            ]}
          />
        </Form.Item>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ formula }) {
  return {
    selectedData: formula.rowdata,
    model: formula.model,
  };
}

export default connect(mapStateToProps, {
  formulaAdd,
  isModelVisible,
  InputChangeValue,
})(AddFormula);
